html {
  font-family: verdana, arial, sans-serif;
  font-size: 12px;
}

body {
  display: flex;
  flex-direction: column;
  background: url("../img/background.jpg");
  background-color: #3a3a3a;
  background-position: 50% 0;
  align-items: center;
}

a {
  text-decoration: none;
}

h2 {
  margin: 10px 0;
  color: #0b81be;
  font-size: 2rem;
  font-weight: normal;
}

h3 {
  font-size: 2rem;
  font-weight: normal;
}

p {
  color: #858585;
  line-height: 1.75;
}

i {
  color: #424242;
}

li {
  line-height: 1.5;
}

.wrapper {
  max-width: 850px;
  padding: 0 15px;
  flex-grow: 1;
}

.header {
  display: flex;
  flex: 1 0 100%;
  margin: 34px 0;
  align-items: center;
}

.header-logo {
  flex: 0 0 200px;
}

.menu {
  position: relative;
  z-index: 9;
  display: flex;
  justify-content: flex-end;
}

.menu__list {
  display: flex;
  min-height: 40px;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: center;
}

.menu__list.active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.menu__list.active .menu__list-item {
  width: 100%;
  margin: 0;
  text-align: center;
}

.menu__list.active .menu__list-item a {
  width: 100%;
  padding: 15px;
  border-radius: 0;
}

.menu__list-item {
  display: block;
  margin-left: 7px;
}

.menu__list-item a {
  display: block;
  padding: 12px 15px;
  text-decoration: none;
  text-transform: uppercase;
  color: #efefef;
  border-radius: 15px;
}

.menu__list-item:hover:not(.active) a {
  background-color: rgba(255, 255, 255, 0.2);
}

.menu__list-item.active a {
  background: linear-gradient(to bottom, #0f8bbe 0%, #2989d8 50%, #207cca 51%, #0eb7fe 100%);
}

.menu__btn {
  flex-direction: column;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.25);
  align-items: center;
  justify-content: center;
}

.menu__btn-row {
  position: relative;
  width: 60%;
  height: 2px;
  margin: 2px 0;
  transition: all 0.5s;
  background-color: #ffffff;
}

.menu__btn.active .menu__btn-row:first-of-type {
  top: 6px;
  transform: rotate(-135deg);
}

.menu__btn.active .menu__btn-row:last-of-type {
  top: -6px;
  transform: rotate(-225deg);
}

.menu__btn.active .menu__btn-row:nth-of-type(2) {
  opacity: 0;
}

@media (min-width: 769px) {
  .menu {
    flex: 1 0 200px;
  }
}

@media (min-width: 851px) {
  .menu__btn {
    display: none;
  }
}

@media (max-width: 850px) {
  .menu__list {
    position: absolute;
    z-index: -999;
    top: 100%;
    right: -15px;
    visibility: hidden;
    flex-direction: column;
    width: 100vw;
    transition: opacity 0.5s;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .menu__btn {
    display: flex;
  }
}

@media (max-width: 768px) {
  .menu {
    flex: 1 0 50px;
  }
}